
import { computed, defineComponent, onMounted, PropType, ref } from "vue";
import WaterLevelChart from "@/components/visual/WaterLevelChart.vue";
import { getWaterLevelByStationAndTime } from "@/api/request";
import { dateFormat, traverseDate } from "@/utils/common";
import { StationType, WaterLevel, WaterLevelChartType } from "@/type";
export default defineComponent({
  components: { WaterLevelChart },
  props: {
    stationInfo: {
      type: Object as PropType<StationType>,
    },
  },
  setup(props) {
    const colors = ["#5470C6", "#91CC75", "#EE6666", "#E5DB4B"];
    const dialogVisible = ref(false);
    const fileList = ref<string[]>([]);
    const skeletonFlag = ref(true);
    const tableData = ref<WaterLevel[]>([]);
    const stationInfo = computed(() => {
      return props.stationInfo;
    });

    const waterLevelChart = ref();
    const loadingFlag = ref(false);
    const keys = computed(() => {
      const res = props.stationInfo?.keys.key;
      return res;
    });
    const keys_cn = computed(() => {
      const res = props.stationInfo?.keysCn.key;
      return res;
    });

    const startDate = new Date();
    const endDate = new Date();
    startDate.setTime(endDate.getTime() - 24 * 3600000);

    const timeValue = ref<[Date, Date]>([
      new Date(dateFormat(startDate.toString(), "yyyy-MM-dd hh") + ":00:00"),
      new Date(dateFormat(endDate.toString(), "yyyy-MM-dd hh") + ":00:00"),
    ]);

    const info = ref<WaterLevelChartType>({
      timeList: [],
      yAxis: [],
      series: [],
      legend: [],
    });

    const disabledHandle = (val: any) => {
      const temp = new Date();
      if (
        val > endDate ||
        val < temp.setTime(endDate.getTime() - 24 * 30 * 3600000)
      ) {
        return true;
      }
      return false;
    };

    const dataHandle = async () => {
      if (props.stationInfo) {
        info.value = {
          timeList: [],
          yAxis: [],
          series: [],
          legend: [],
        };
        const station = props.stationInfo.name;
        const type = props.stationInfo.type;
        let start = 50;
        props.stationInfo.keysCn.key.forEach((key, index) => {
          if (index >= 2) {
            info.value.yAxis.push({
              alignTicks: true,
              axisLine: {
                show: true,
                lineStyle: {
                  color: colors[index],
                },
              },

              type: "value",
              offset: start,
            });
            start += 50;
          } else {
            info.value.yAxis.push({
              alignTicks: true,
              type: "value",
              axisLine: {
                show: true,
                lineStyle: {
                  color: colors[index],
                },
              },
            });
          }
          info.value.legend.push(key);
          info.value.series.push({
            name: key,
            data: [],
            type: "line",
            smooth: true,
            yAxisIndex: index,
            itemStyle: {
              normal: {
                color: colors[index],
                lineStyle: {
                  color: colors[index],
                },
              },
            },
          });
        });
        const data = await getWaterLevelByStationAndTime(
          type,
          station,
          dateFormat(timeValue.value[0].toString(), "yyyy-MM-dd hh") + ":00:00",
          dateFormat(timeValue.value[1].toString(), "yyyy-MM-dd hh") + ":00:00"
        );
        if (data != null && (data as any).code === 0) {
          tableData.value = data.data;
          tableData.value.reverse();
          (data.data as any).forEach((item: any) => {
            info.value.timeList.push(item.time.substring(10, 16));
            props.stationInfo!.keys.key.forEach((key, index) => {
              info.value.series[index].data.push(item[key]);
            });
          });
        }
      }
    };

    const btnClick = async () => {
      loadingFlag.value = true;
      await dataHandle();
      waterLevelChart.value.refreshData();
      loadingFlag.value = false;
    };

    const getFileList = () => {
      const timestamp = Date.now();
      const timeList1 = traverseDate(
        props.stationInfo!.startTime.key[0],
        timestamp
      );
      const timeList2 = traverseDate(
        props.stationInfo!.startTime.key[1],
        timestamp
      );
      const name_en = props.stationInfo?.nameEn;
      for (let i = timeList1.length - 1; i >= 0; i--) {
        fileList.value.push(name_en + timeList1[i] + ".txt");
      }
      for (let i = timeList2.length - 1; i >= 0; i--) {
        fileList.value.push(name_en + "UTC+8" + timeList2[i] + ".txt");
      }

    };

    const downloadClick = (name: string) => {
      if (name.indexOf("UTC+") === -1) {
        window.location.href = `${process.env.VUE_APP_WATERLEVEL_ADDRESS}download/downloadOne1/${name}`;
      } else {
        window.location.href = `${process.env.VUE_APP_WATERLEVEL_ADDRESS}download/downloadOne2/${name}`;
      }
    };

    onMounted(async () => {
      skeletonFlag.value = true;
      getFileList();
      await dataHandle();
      skeletonFlag.value = false;
    });

    return {
      stationInfo,
      info,
      skeletonFlag,
      tableData,
      keys,
      keys_cn,
      timeValue,
      disabledHandle,
      loadingFlag,
      btnClick,
      waterLevelChart,
      dialogVisible,
      getFileList,
      fileList,
      downloadClick,
    };
  },
});
