
import { computed, defineComponent, PropType } from "vue";
import { ParkType } from "@/type";
export default defineComponent({
  props: {
    parkInfo: {
      type: Object as PropType<ParkType>,
    },
  },
  setup(props) {
    const parkInfo = computed(() => {
      return props.parkInfo;
    });

    return {
      parkInfo,
    };
  },
});
