
import { defineComponent, onMounted, PropType, ref } from "vue";
import * as echarts from "echarts";
import { WaterLevelChartType } from "@/type";
export default defineComponent({
  props: {
    info: {
      type: Object as PropType<WaterLevelChartType>,
    },
  },
  setup(props) {
    const chart = ref<HTMLElement>();
    let myChart: echarts.ECharts;

    let option: any = {};

    const init = () => {
      if (props.info) {
        option = {
          title: {
            padding: [10, 0, 0, 160],
            text: "",
            textStyle: {
              fontFamily: "Microsoft YaHei",
            },
          },
          tooltip: {
            trigger: "axis",
            axisPointer: {
              type: "cross",
            },
          },
          legend: {
            data: props.info.legend,
            top: 10,
          },
          grid: {
            top: 50,
            right: 50 * (props.info.series.length - 1),
          },

          dataZoom: [
            {
              xAxisIndex: [0],
              filterMode: "filter",
              start: 50,
              end: 100,
            },
          ],
          xAxis: {
            name: "",
            type: "category",
            data: props.info.timeList,

            nameTextStyle: {
              width: 20,
              overflow: "none",
              textBorderType: "solid",
            },
          },
          yAxis: props.info.yAxis,
          series: props.info.series,
        };
      }
    };

    const refreshData = () => {
      init();
      myChart.setOption(option);
    };

    onMounted(() => {
      init();
      myChart = echarts.init(chart.value as HTMLElement);
      myChart.setOption(option);
    });

    return {
      chart,
      refreshData,
    };
  },
});
