
import { defineComponent, reactive, ref } from "vue";
export default defineComponent({
  emits: ["selectBasemap"],
  setup(_, context) {
    const list = reactive([
      {
        image: "/tianditu.png",
        name: "",
      },
      {
        image: "/satellite.png",
        name: "",
      },

      {
        image: "/osm.png",
        name: "osm",
      },
    ]);
    const active = ref(0);

    const basemapClick = (index: number) => {
      if (index !== active.value) {
        active.value = index;
        context.emit("selectBasemap", active.value);
      }
    };

    return { list, active, basemapClick };
  },
});
