
import { computed, defineComponent, PropType } from "vue";
import { BuoyType } from "@/type";
export default defineComponent({
  props: {
    buoyInfo: {
      type: Object as PropType<BuoyType>,
    },
  },
  setup(props) {
    const buoyInfo = computed(() => {
      return props.buoyInfo;
    });

    return {
      buoyInfo,
    };
  },
});
