
import { computed, defineComponent, PropType } from "vue";
import { AnchorType } from "@/type";
export default defineComponent({
  props: {
    anchorInfo: {
      type: Object as PropType<AnchorType>,
    },
  },
  setup(props) {
    const anchorInfo = computed(() => {
      return props.anchorInfo;
    });

    return {
      anchorInfo,
    };
  },
});
