
import { computed, defineComponent, nextTick, onMounted, ref } from "vue";

const __default__ = defineComponent({
  props: {
    content: {
      type: Array,
    },
  },
  setup(props) {
    const content = computed(() => {
      return props.content;
    });

    const width = computed(() => {
      if (props.content) {
        return props.content?.length * -500 + "px";
      } else {
        return "";
      }
    });

    const time = computed(() => {
      //   return (
      //     (((props.content as any).length * 500) / document.body.clientWidth - 100) / 10 +
      //     "s"
      //   );
      return ((props.content as any).length * 500) / 50 + "s";
    });

    const replaceHandle = (currentStr: string) => {
      const res = new RegExp("()", "g");
      currentStr = currentStr.replace(
        res,
        "<span style='color:red; margin: 0px 5px'></span>"
      );
      return currentStr;
    };

    return {
      content,
      replaceHandle,
      time,
      width,
    };
  },
});

