
import { defineComponent, onMounted, ref } from "vue";
import {
  SearchTable,
  BuoyType,
  AnchorType,
  ParkType,
  BridgeType,
  StationType,
  ShipType,
} from "@/type";
import { pageList } from "@/api/request";
export default defineComponent({
  emits: ["returnPoint", "shipDataModeChange"],
  setup(_, context) {
    const inputValue = ref("");
    let keyword = "";
    const selectValue = ref("ship");
    const showFlag = ref(false);
    const skeletonFlag = ref(false);
    const tableData = ref<SearchTable[]>([]);

    const switchValue = ref(true);

    const currentPage = ref(1);
    const total = ref(0);

    const options = [
      {
        label: "AIS",
        value: "ship",
      },
      {
        label: "",
        value: "buoy",
      },
      {
        label: "",
        value: "park",
      },
      {
        label: "",
        value: "anchor",
      },
      {
        label: "",
        value: "bridge",
      },
      {
        label: "",
        value: "station",
      },
    ];
    const optionMap = {
      ship: "AIS",
      buoy: "",
      park: "",
      anchor: "",
      bridge: "",
      station: "",
    };
    const propLabelMap = {
      ship: [
        { prop: "mmsi", label: "mmsi" },
        { prop: "nameCn", label: "" },
      ],
      buoy: [
        { prop: "waterway", label: "" },
        { prop: "name", label: "" },
      ],
      park: [
        { prop: "name", label: "" },
        { prop: "usage", label: "" },
      ],
      anchor: [
        { prop: "anchorName", label: "" },
        { prop: "usage", label: "" },
      ],
      bridge: [
        { prop: "bridgeName", label: "" },
        { prop: "bridgeAttributes", label: "" },
      ],
      station: [{ prop: "name", label: "" }],
    };

    const focusHandle = async () => {
      if (showFlag.value == false) {
        showFlag.value = true;
        skeletonFlag.value = true;
        await getData(0, 10);
        skeletonFlag.value = false;
      }
    };

    const closeHandle = () => {
      showFlag.value = false;
    };

    const getData = async (page: number, size: number) => {
      let type = selectValue.value;
      if (switchValue.value && selectValue.value == "ship") {
        type = "realShip";
      }
      const data = await pageList({ type, page, size, keyword });
      if (data != null && (data as any).code === 0) {
        tableData.value = data.data.list;
        total.value = data.data.total;
      }
    };

    const pageChange = async () => {
      skeletonFlag.value = true;

      await getData(currentPage.value - 1, 10);
      skeletonFlag.value = false;
    };

    const changeHandle = async () => {
      skeletonFlag.value = true;
      keyword = "";
      await getData(currentPage.value - 1, 10);
      skeletonFlag.value = false;
      currentPage.value = 1;
    };

    const replaceHandle = (currentStr: string) => {
      const res = new RegExp("(" + keyword + ")", "g");
      currentStr = currentStr.replace(
        res,
        "<span style='color:red;'>" + keyword + "</span>"
      );
      return currentStr;
    };

    const searchClick = async () => {
      keyword = inputValue.value;
      skeletonFlag.value = true;
      await getData(currentPage.value - 1, 10);
      skeletonFlag.value = false;
      currentPage.value = 1;
    };

    const rowClickHnadle = (
      row:
        | BuoyType
        | BridgeType
        | ParkType
        | AnchorType
        | StationType
        | ShipType
    ) => {
      if ("noMeaning" in row) {
        context.emit("returnPoint", {
          point: [row.longitude, row.latitude],
          info: row,
        });
      } else if ("keysCn" in row) {
        context.emit("returnPoint", {
          point: [row.longitude, row.latitude],
          info: row,
        });
      } else if ("polygon" in row) {
        context.emit("returnPoint", {
          point: row.polygon.coordinates[0][0],
          info: row,
        });
      } else if ("waterwayId" in row) {
        console.log("1", row);
        context.emit("returnPoint", {
          point: [row.longitude, row.latitude],
          info: row,
        });
      } else if ("anchorName" in row) {
        context.emit("returnPoint", {
          point: [row.longitude, row.latitude],
          info: row,
        });
      } else if ("mmsi" in row) {
        if (switchValue) {
          context.emit("returnPoint", {
            point: [row.longitude, row.latitude],
            info: row,
          });
        }
      }
    };

    const switchChange = async (val: boolean) => {
      context.emit("shipDataModeChange", val);
      if (selectValue.value === "ship") {
        skeletonFlag.value = true;
        await getData(0, 10);
        skeletonFlag.value = false;
      }
    };


    return {
      inputValue,
      selectValue,
      switchValue,
      options,
      showFlag,
      focusHandle,
      closeHandle,
      optionMap,
      propLabelMap,
      tableData,
      skeletonFlag,
      total,
      currentPage,
      pageChange,
      changeHandle,
      replaceHandle,
      searchClick,
      rowClickHnadle,
      switchChange,
    };
  },
});
